.page-title {
  margin: 30px;
  font-size: 30px;
  font-weight: 900;
  text-align: center; }

.sub-title {
  margin: 30px;
  font-size: 24px;
  font-weight: 900; }

.page-top {
  padding-top: 6rem; }

.book-slot {
  margin: 10px;
  padding: 1rem;
  display: flex;
  list-style: none;
  border-radius: 8px;
  background-color: white;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.301); }

.card-slot {
  width: 120px;
  flex-direction: column;
  align-items: center; }
  .card-slot img {
    height: 100px; }
  .card-slot h3 {
    font-size: 14px;
    font-weight: 700;
    display: block;
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis; }

.container {
  width: 70%; }
